@import "../../scss/common.pallets-style-colors.global";
md-nav-list{
	.md-list-item:hover, .md-list-item.md-list-item-focus {
		background-color: #1c1e20!important;
		.material-icons{
			color: $yellow;
		}
		/* &:before{
			left: 0;
		} */
	} 
	.md-list-item{
		position: relative;
		background-color: #1c1e20;
		color: #fff;
		padding: 10px;
		margin-bottom: 12px;
		padding-left: 30px!important;
		.material-icons{
			margin-right: 15px;
			transition: color linear 0.2s;
			vertical-align: middle;
		}
		&:before{
			position: absolute;
			top: 0;
			left: -10px;
			width: 5px;
			height: 100%;
			content: '';
			z-index: 2;
			background:  $yellow;
			transition: left linear 0.5s;
		}
	}
	.md-list-item.active{
		&:before{
			left: 0;
		}
		.material-icons{
			color: $yellow;
		}
	}
}
md-nav-list .md-list-item:hover, md-nav-list .md-list-item.md-list-item-focus {
	outline: none;
	background: rgba(255, 255, 255, 0.04);
}
.avatar{
	font-size: 60px;
	margin-right: 10px;
	height: 100px;
}
.user-wrapper{
    padding: 40px 10px 25px 20px;
    margin-bottom: 40px;
}
.caption{
	padding: 0 10px;
}



