.job-list-header {
    background: #333;
    padding: 10px;
    color: #fff;
    .material-icons{
    	color: #fff;
    }
}
.job-list-panel{
	background: #efefef;
}
.job-list-item{
	background: #efefef;
}
.job-item{
	border-radius: 10px;
	background: #fff;
	padding: 10px;
}
.job-item{
	position: relative;
	.caption{
		display: inline-block;
		margin-left: 10px;
		border-left: 1px solid #333;
	}
	.list-btn{
		position: absolute;
	    right: 10px;
	    top: 35%;
	}
}
.skip_previous{
	color: #b9b9b9;
	font-size: 32px;
}
.play_arrow{
	color: #169bd5;
	font-size: 32px;
}
.pull-right{
	.mdl-menu {
	    right: 0;
	    left: auto;
	}	
}
.search-container{
	display: inline-block;
}
.margin-top-hr{
	margin-top: 11px;
}



