fw-upgrade[open]{
	dialog {
		display: block;
		z-index: 99;
		zoom: 1;
		transition: all 0.5s linear;
	}
	.dialog-wrapper{
		display: block;
		opacity: 1;
		
	}	
}
.dialog-wrapper{
	background: rgba(0,0,0,0.3);
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 90;
	display: none;
	opacity: 0;
	transition: all 0.5s linear;
}
dialog {
	zoom: 0;
}
.mdl-dialog{
	width: 600px;
	margin: 50px auto;
}
label{
	i{
		vertical-align: middle;
		margin-right: 5px;
		margin-top: -2px;
	}
}
.input-group{
	label{
		margin-right: 10px;
		width: 155px;
	}
}
.input{
	width: 210px;
}
.well{
	border-radius: 0;
}
.well.small{
	padding: 5px;
}
.input.small{
	width: 100px;
	margin-right: 10px;
	&:last-child{
		margin-right: 0;
	}
}
.mdl-dialog__actions{
	button{
		margin-right: 5px!important;
		&:first-child{
			margin-right: 0!important;
		}
	}
}
hr{
	opacity: 0.4;
}
.input-group{
	md-slide-toggle{
		display: inline-block!important;
		vertical-align: middle;
		margin: 0;
		margin-bottom: 5px;
	}	
}



