@import '../../scss/common.pallets-style-colors.global';
@keyframes md-ink-ripple-anim {
  0% {
    opacity: 0
  }
  50%{
    opacity: 1
  }
  100% {
    opacity: 0
  }
}
[layout-fill]{
  background-color: $white;
}
.search-container{
  position: relative;
  .material-icons{
    vertical-align: middle;
  }
}
.check_circle{
  color: green;
}
.cancel{
  color: #c72020;
}
.v-align-m{
  vertical-align: middle;
}
.field{
  cursor: default;
}
.md-checkbox-layout{
  vertical-align: middle;
  .md-checkbox-inner-container{
    margin: 0!important;
  }
  .md-ink-ripple{
    // animation: md-ink-ripple-anim .5s linear;
    display: none;
  }
} 
.transition-delay{
	transition-delay: 1s;
}
.unstyled-list2{
 list-style: none;
 padding: 0;

 li{
  width: 100%;
  transition: 1s linear all !important;
  float: left;
 }
 .inline-block{
  //display: inline-block;
  width: 33.33333333%;
  overflow: hidden;
  // float: left;
  box-sizing: border-box;
}
}

.xxx{
  transition: 1s linear all !important;
  width: 0 !important;
}
md-data-table th, md-data-table td, .md-data-table th, .md-data-table td{
  padding: 0 10px!important;
}
.table-action{
  background-color: $gray-color-700;
}
.drag-able{
  resize: vertical;
  overflow: hidden;
  padding-bottom: 10px;
  position: relative;
  &:after{
    content: '***';
    height: 10px;
    line-height: 17px;
    width: 100%;
    display: block;
    cursor: n-resize;
    background-color: rgb(224, 224, 224);
    position: absolute;
    bottom: 0;
    text-align: right;
  }
}
.status-bage{
    height: 30px;
    display: inline-block;
    width: 4px;
    vertical-align: middle;
    margin-right: 10px; 
}
.healty{
  background-color: green;
}
.stoped{
  background-color: yellow;
}
.updatig{
  background-color: gray;
}
.error{
  background-color: red;
}
.active-color{
  background-color: #dcdcdc;
}
.mdl-paging{
  padding: 10px 0;
}



